<template>
    <div>
        <div class="page-titles">
            <div class="row">
                <div class="col-12 col-sm-6">
                    <h3 class="text-themecolor">{{trans('configuration.locale')}}
                        <span class="card-subtitle d-none d-sm-inline" v-if="locales.total">{{trans('general.total_result_found',{count : locales.total, from: locales.from, to: locales.to})}}</span>
                        <span class="card-subtitle d-none d-sm-inline" v-else>{{trans('general.no_result_found')}}</span>
                    </h3>
                </div>
                <div class="col-12 col-sm-6">
                    <div class="action-buttons pull-right">
                        <button class="btn btn-info btn-sm" v-if="!showWordPanel" @click="showWordPanel = !showWordPanel"><i class="fas fa-plus"></i> <span class="d-none d-sm-inline">{{trans('configuration.add_new_word')}}</span></button>
                        <button class="btn btn-info btn-sm" v-if="locales.total && !showCreatePanel" @click="showCreatePanel = !showCreatePanel"><i class="fas fa-globe"></i> <span class="d-none d-sm-inline">{{trans('configuration.add_new_locale')}}</span></button>
                        <help-button @clicked="help_topic = 'configuration.locale'"></help-button>
                    </div>
                </div>
            </div>
        </div>
        <div class="container-fluid">
            <transition name="fade">
                <div class="card card-form" v-if="showCreatePanel">
                    <div class="card-body">
                        <h4 class="card-title">{{trans('configuration.add_new_locale')}}</h4>
                        <show-tip module="configuration" tip="tip_locale"></show-tip>
                        <locale-form @completed="getLocales" @cancel="showCreatePanel = !showCreatePanel"></locale-form>
                    </div>
                </div>
            </transition>

            <transition name="fade">
                <div class="card card-form" v-if="showWordPanel">
                    <div class="card-body">
                        <h4 class="card-title m-t-20">{{trans('configuration.add_new_word')}}</h4>
                        <show-tip module="configuration" tip="tip_locale_word"></show-tip>
                        <form @submit.prevent="addWord" @keydown="addWordForm.errors.clear($event.target.name)">
                            <div class="row">
                                <div class="col-12 col-sm-4">
                                    <div class="form-group">
                                        <label for="">{{trans('configuration.locale_word')}}</label>
                                        <input class="form-control" type="text" v-model="addWordForm.word" name="word" :placeholder="trans('configuration.locale_word')">
                                        <show-error :form-name="addWordForm" prop-name="word"></show-error>
                                    </div>
                                </div>
                                <div class="col-12 col-sm-4">
                                    <div class="form-group">
                                        <label for="">{{trans('configuration.locale_module')}}</label>
                                        <select v-model="addWordForm.module" class="custom-select col-12" @change="addWordForm.errors.clear('module')">
                                          <option value=null selected>{{trans('general.select_one')}}</option>
                                          <option v-for="module in modules" v-bind:value="module">
                                            {{ toWord(module) }}
                                          </option>
                                        </select>
                                        <show-error :form-name="addWordForm" prop-name="module"></show-error>
                                    </div>
                                </div>
                                <div class="col-12 col-sm-4">
                                    <div class="form-group">
                                        <label for="">{{trans('configuration.locale_translation')}}</label>
                                        <input class="form-control" type="text" v-model="addWordForm.translation" name="translation" :placeholder="trans('configuration.locale_translation')">
                                        <show-error :form-name="addWordForm" prop-name="translation"></show-error>
                                    </div>
                                </div>
                            </div>
                            <div class="card-footer text-right">
                                <button type="button" class="btn btn-danger waves-effect waves-light " @click="showWordPanel = !showWordPanel">{{trans('general.cancel')}}</button>
                                <button type="submit" class="btn btn-info waves-effect waves-light">{{trans('general.add')}}</button>
                            </div>
                        </form>
                    </div>
                </div>
            </transition>

            <div class="card">
                <div class="card-body">
                    <div class="table-responsive" v-if="locales.total">
                        <table class="table table-sm">
                            <thead>
                                <tr>
                                    <th>{{trans('configuration.locale_name')}}</th>
                                    <th>{{trans('configuration.locale')}}</th>
                                    <th class="table-option">{{trans('general.action')}}</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="locale in locales.data">
                                    <td v-text="locale.name"></td>
                                    <td v-text="locale.locale"></td>
                                    <td class="table-option">
                                        <div class="btn-group">
                                            <button class="btn btn-info btn-sm" v-tooltip="trans('configuration.edit_locale')" @click.prevent="editLocale(locale)"><i class="fas fa-edit"></i></button>
                                            <router-link :to="`/configuration/locale/${locale.locale}/auth`" class="btn btn-success btn-sm" v-tooltip="trans('locale.translation')"><i class="fas fa-arrow-circle-right"></i></router-link>
                                            <button class="btn btn-danger btn-sm" :key="locale.id" v-if="locale.locale !== 'en'" v-confirm="{ok: confirmDelete(locale)}" v-tooltip="trans('configuration.delete_locale')"><i class="fas fa-trash"></i></button>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <module-info v-if="!locales.total" module="configuration" title="locale_module_title" description="locale_module_description" icon="list"></module-info>
                    <pagination-record :page-length.sync="filter.page_length" :records="locales" @updateRecords="getLocales" @change.native="getLocales"></pagination-record>
                </div>
            </div>
        </div>
        <right-panel :topic="help_topic"></right-panel>
    </div>
</template>


<script>
    import localeForm from './form'

    export default {
        components : { localeForm },
        data() {
            return {
                locales: {
                    total: 0,
                    data: []
                },
                filter: {
                    page_length: helper.getConfig('page_length')
                },
                addWordForm: new Form({
                    word: '',
                    translation: '',
                    module: ''
                }),
                showCreatePanel: false,
                showWordPanel: false,
                modules: [],
                help_topic: ''
            };
        },
        mounted(){
            if(!helper.hasPermission('access-configuration')){
                helper.notAccessibleMsg();
                this.$router.push('/dashboard');
            }

            if(!helper.featureAvailable('multilingual')){
                helper.featureNotAvailableMsg();
                this.$router.push('/dashboard');
            }

            this.getLocales();
        },
        methods: {
            getLocales(page){
                let loader = this.$loading.show();
                if (typeof page !== 'number') {
                    page = 1;
                }
                let url = helper.getFilterURL(this.filter);
                axios.get('/api/locale?page=' + page + url)
                    .then(response => {
                        this.locales = response.locales;
                        this.modules = response.modules;
                        loader.hide();
                    })
                    .catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            editLocale(locale){
                this.$router.push('/configuration/locale/'+locale.id+'/edit');
            },
            confirmDelete(locale){
                return dialog => this.deleteLocale(locale);
            },
            deleteLocale(locale){
                let loader = this.$loading.show();
                axios.delete('/api/locale/'+locale.id)
                    .then(response => {
                        toastr.success(response.message);
                        this.getLocales();
                        loader.hide();
                    }).catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            addWord(){
                let loader = this.$loading.show();
                this.addWordForm.post('/api/locale/add-word')
                    .then(response => {
                        toastr.success(response.message);
                        loader.hide();
                    })
                    .catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            toWord(word){
                return helper.toWord(word);
            }
        },
        filters: {
          momentDateTime(date) {
            return helper.formatDateTime(date);
          }
        }
    }
</script>
